@import "/src/App.scss";

.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 50px;
  width: inherit;
  padding: 0px 5px;
  color: inherit;
}

.navbar-items {
  width: inherit;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.App_name {
  font-weight: bold;
  text-decoration: none;
  font-size: 1.3rem;
  color: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
  h1 {
    margin: 0px;
    padding: 0px;
  }
}

#option-wrapper {
  padding: 0px;
  margin: 0px;
  display: inherit;
}

.enable-search {
  width: 35px;
  height: 35px;
  border-style: none;
  background: none;
  font-size: 1.2rem;
  font-weight: bold;
  outline: none;
  border-radius: 50%;
  color: inherit;
  padding: 0px;
  i {
    vertical-align: text-bottom;
  }
}

.opt {
  color: #4d8aab;
}

.new-user-msg::after {
  content: "";
  position: absolute;
  width: 10px; /* Adjust the size of the dot */
  height: 10px;
  background: rgb(245, 83, 2); /* Adjust the color of the dot */
  border-radius: 50%;
  transform: translate(-50%, -50%);
  animation: blink 1s ease-in-out 0s infinite;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.options_slider {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 3s ease;
}

.nav_icons {
  width: 35px;
  height: 35px;
  border-radius: 50%;
}

#nav-dropdown {
  color: inherit;
  height: auto;
  width: fit-content;
  z-index: 21;
  position: absolute;
  top: 50px;
  right: 10px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 12px;
  max-width: 200px;

  #nav-dropdown-menu {
    border-radius: 12px;
    width: 100%;
    height: auto;
    padding: 0px;
    margin: 0px;
    list-style: none;
    backdrop-filter: blur(19px) saturate(180%);
    -webkit-backdrop-filter: blur(19px) saturate(180%);
    background-color: rgba(255, 255, 255, 0.987);
    border: 1px solid rgba(255, 255, 255, 0.125);
    background: inherit;
    #current-user {
      border-top: none;
      text-align: center;
      font-weight: bold;
      font-size: 0.8em;
      line-height: 1.5;
      padding: 5px;
      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }
    }
    li {
      word-wrap: break-word;
      border-top: 1px solid rgba(25, 24, 24, 0.263);

      a,
      button {
        display: inline-block;
        width: 100%;
        height: 100%;
        padding: 10px;
        margin: 0px !important;
        text-decoration: none;
        color: inherit;
        font-size: 0.9rem;

        i {
          padding: 2px;
          vertical-align: sub;
          margin-right: 5px;
        }
      }

      button {
        cursor: pointer;
        outline: none;
        background: none;
        text-align: start;
        border: transparent;
      }
    }
  }
}

.hideOptions {
  width: fit-content;
  .options {
    display: none;
  }
}

.showOptions {
  width: fit-content;
}

