@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;700&family=M+PLUS+Rounded+1c:wght@500&family=Orbitron&family=Quantico&family=Teko:wght@300&display=swap");
* {
  font-family: Comfortaa, Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  text-decoration: none;
  overflow: auto;
}

html,
body {
  margin: 0px;
  padding: 0px;
  height: 100vh;
  width: 100vw;
}

*::-webkit-scrollbar {
  width: 16px;
}

*::-webkit-scrollbar-track {
  background-color: none;
  border-radius: 5px;
}

*::-webkit-scrollbar-thumb {
  cursor: pointer;
  border-radius: 10px;
  border: 5px solid transparent;
  background-clip: content-box;
  background-color: #514d65;
}

$default_Light_Hover: rgba(109, 106, 106, 0.241);
$default_dark_hover: black;
$default_radius: 20px !important ;

.dark {
  background: #000000;
  color: #ffffff;
  .components_background {
    background: #1e1d1d;
  }
}

.light {
  background: #ffffff;
  color: #000000;
  .components_background {
    background: #ffffffbf;
  }
}

#AppLoading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background: white !important;
  img {
    width: 175px;
    height: 175px;
  }
  h2 {
    margin: 0px;
    font-weight: 100;
  }
}
//// Signup and Signin css////////////////////////////////////🔽🔽

.auth-container {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

.auth-box {
  padding: 20px;
  z-index: 1;
  position: relative;
  height: auto;
}

.authHeader {
  position: relative;
  width: 100%;
  height: 100px;
  #appNameAndLogo {
    display: flex;
    align-items: center;
    color: inherit;
    img {
      display: inline-block;
      width: 50px;
      height: 50px;
      margin-right: 5px;
      border-radius: 50%;
      vertical-align: auto;
    }
    h2 {
      font-weight: bolder;
      margin: 0px;
      display: inline-block;
    }
  }
  p {
    font-size: 1.3rem;
    font-weight: bold;
    margin: 10px 0px 0px 3px;
  }
}

#signin {
  max-height: 80%;
  width: 35%;
  color: inherit;
}

#signup {
  max-height: 95%;
  min-width: 30%;
  width: 45%;
}

.authInputBox {
  position: relative;
  width: 100%;
  height: auto;
}

.auth-form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  input:focus {
    border: 2px solid rgb(6, 126, 218);
  }
  ::placeholder {
    color: #a29b9b;
    font-size: 1rem;
    font-weight: 600;
  }
}

.pv {
  margin: 5px 0px;
  label {
    font-family: monospace;
    margin: 0px;
    vertical-align: text-top;
    cursor: pointer;
  }

  label:hover {
    text-decoration: underline;
  }

  input {
    width: 15px;
    height: 15px;
  }
}

.input_wrapper {
  position: relative;
  height: auto;
  margin: 2px 0px !important;

  label {
    padding: 0px;
    position: relative;
    width: 100%;
    height: auto;
  }

  input {
    width: 100%;
    padding: 0px 5px;
    margin-top: 22px;
    border-radius: 5px;
    border: 0.2px solid rgb(74, 72, 75);
    outline: none;
    height: 35px;
    font-weight: bold;
  }

  input:focus {
    border: 2px solid rgb(6, 126, 218);
  }

  input::placeholder {
    opacity: 0;
  }
  .spanPlaceholder,
  .birthdayPlaceholder {
    position: absolute;
    top: -20px;
    left: 5px;
    transform: translateY(20px);
    font-size: 1em;
    font-weight: bold;
    transition-duration: 300ms;
    color: #948e8e;
  }

  .birthdayPlaceholder {
    opacity: 0;
  }

  label:focus-within > .spanPlaceholder,
  input:not(:placeholder-shown) + .spanPlaceholder {
    color: inherit;
    transform: translateY(-5px);
    font-size: 0.8em;
  }
  label:focus-within > .birthdayPlaceholder {
    opacity: 1;
    color: inherit;
    transform: translateY(-5px);
    font-size: 0.8em;
  }
}

.input_error {
  border: 2px solid rgb(255, 0, 0);
}

#forgotPassword {
  text-align: right;
  position: relative;
  padding: 5px;

  .link {
    font-weight: bold;
    text-decoration: none;
  }
}

#signup_Btn,
#signin_Btn {
  font-weight: bold;
}

.inputsBtn {
  height: 130px;
  display: flex;
  align-items: end;
  justify-content: space-between;
}

.next {
  p,
  input {
    margin: 0px;
  }
}

.continueBtn {
  border-radius: 5px;
  padding: 5px;
  height: 35px;
  display: inline-block;
  width: fit-content;
  border: none !important;
  background: #322ded;
  font-weight: bold;
  cursor: pointer;
  font-size: 1rem;
  color: #ffffff;
}

@media (max-width: 950px) {
  #signin {
    max-height: 70%;
    width: 50%;
  }

  #signup {
    max-height: 95%;
    width: 60%;
  }

  .authHeader {
    height: auto;
    min-height: 100px;
    img {
      width: 50px;
      height: 50px;
    }
    p {
      font-size: 1rem;
      margin-top: 5px;
    }
  }
  .inputsBtn {
    height: 110px;
  }
}

@media (max-width: 520px) {
  #signin {
    max-height: 70%;
    width: 95%;
  }

  #signup {
    max-height: 95%;
    width: 95%;
  }
  .authHeader {
    height: auto;
    min-height: 100px;
    img {
      width: 45px;
      height: 45px;
    }
  }
  .inputsBtn {
    height: 90px;
  }

  .next {
    p,
    input {
      font-size: 0.8rem;
    }
  }
}

////////////////////////////////////////////////////////////////////

.verification_container_startup {
  backdrop-filter: blur(50px) saturate(100%);
  -webkit-backdrop-filter: blur(10px) saturate(50%);
  background-color: rgba(255, 255, 255, 0);
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  transition: 0.2s linear;
  z-index: 2;
  width: 100%;
  top: 0px;
}

.isLoading {
  padding: 10px;
  text-align: center;
}

.app-layout {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  transition: 0.3s linear;
  background-position: center;
  background-repeat: no-repeat;
  background-clip: border-box;
  background-size: cover;

  #offline {
    div {
      text-align: center;
    }
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 200;
    top: 0px;
    left: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(30px) saturate(100%);
    -webkit-backdrop-filter: blur(10px) saturate(50%);
    background-color: rgba(49, 47, 47, 0.547);
    img {
      min-width: 250px;
      width: 100%;
      max-height: 80%;
      height: auto;
      object-fit: contain;
    }
    button {
      margin: 5px;
    }
  }

  #alert_container {
    position: absolute;
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;
    left: 0px;
    bottom: 15%;
  }

  .red_alert {
    border: 2px solid red;
  }
  .green_alert {
    border: 2px solid green;
  }

  #Alert {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2px;
    min-width: 10%;
    max-width: 80%;
    word-wrap: break-word;
    border-radius: 10px;
    font-weight: bold;
    svg {
      margin-right: 5px;
      background: none;
      position: relative;
      height: 20px;
      width: 20px;
    }
    span {
      font-size: 0.8em;
    }
  }
}

.glass {
  backdrop-filter: blur(19px) saturate(180%);
  -webkit-backdrop-filter: blur(19px) saturate(180%);
  background-color: rgba(255, 255, 255, 0.987);
  border: 1px solid rgba(255, 255, 255, 0.125);
  border-radius: 10px;
}

/////////////// header of settings ///////////////

