///////// contacts list in sidechats and contents//////////

.contents-container {
  padding: 0px;
  margin: 0px !important;

  .contactsList {
    transition: 0.2s linear;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 0.5px solid rgba(64, 63, 63, 0.184);
    margin: 5px 0px;
    padding: 0px 2px;
    cursor: pointer;
  }

  .contactsList:hover {
    background: #888d9753;
    border-radius: 10px;
  }
}

.avatarList {
  width: 55px;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 5px;
  border: 1px soild rgb(140, 0, 255);
}

.contactsAvatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.nameList {
  width: 100%;
  padding: 0px 5px;
  position: relative;
  border: 1px soild yellow;

  .contact_last_info {
    width: 100%;
    display: flex;
    justify-content: space-between;
    pointer-events: none;
    font-weight: bold;
    font-size: 0.7rem;
    padding: 1px 0px;

    .contact_msg_info,
    .last_msg_time {
      font-size: 0.7rem;
      display: inline;
    }

    .online_status {
      width: 5px;
      height: 5px;
      background-color: rgb(8, 233, 38);
      border-radius: 50%;
      display: inline-block;
      margin: 0px 10px 0px 5px;
      box-shadow: 0px 0px 3px 3px rgb(2, 146, 21);
      vertical-align: middle;
    }
  }
}

.name_and_count {
  display: flex;
  justify-content: space-between;
  padding: 0px;
  .contact_name {
    font-size: 0.9rem;
    font-weight: bold;
  }
  .unread_msg_count {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(46, 144, 230);
    min-width: 20px;
    height: 20px;
    border-radius: 5px;
    font-size: 0.7em;
  }
  .invisible {
    //To hide unread_msg_counter, if no unread message
    visibility: hidden !important;
  }
  p {
    padding: 0px;
    margin: 0px;
    font-size: 0.9em;
  }
}

.delivered_seen {
  padding: 0px 2px 0px 5px;
  font-size: 0.7rem;
  vertical-align: middle;
}

.unblock_btn {
  float: right;
}

.unblock_btn:hover {
  color: green;
}

@media screen and (max-width: 550px) {
  .Header {
    padding: 0px 10px;
  }

  .delivered_seen {
    font-size: 0.7rem;
  }

  .nameList {
    .contact {
      padding: 10px 5px 0px 5px;
    }

    .contact_last_info {
      margin: 0px;
    }
  }
  .contact_last_info span {
    font-size: 0.7em;
  }
  .contact_name {
    p {
      font-size: 0.8em;
    }
  }
}

