.settings {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.settings_window {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  transition: 0.3s linear;
  width: 100%;
  height: calc(100vh - 50px);
}

.setting_options,
.selected_settings {
  height: 100%;
  padding: 10px;
}

.setting_options {
  width: 28%;
  background: rgba(187, 188, 190, 0.221) !important;
  border-top-right-radius: 12px;
}

.selected_settings {
  width: 72%;
  height: 100%;
  position: relative;
}

.modified_settings_Btn {
  border: none;
  width: 100%;
}

.current_setting {
  width: 100%;
  border: 4px solid rgb(2, 124, 245) !important;
  transition: 0.2s linear;
  background: #888d9753 !important;
  border-top: none !important;
  border-right: none !important;
  border-bottom: none !important;
}

.settings_header {
  min-height: 8%;
  div {
    display: flex;
    align-items: center;
    button {
      display: inline;
    }
    h2 {
      margin: 0px 10px;
      display: inline;
    }
  }
}

#opened_setting {
  position: relative;
  height: auto;
  min-height: 90%;
}

.small_header {
  text-align: start;
  padding: 5px;
  background: #888d9753;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  font-size: 1.2em;
}

#dark_mode_category {
  width: 100%;
  padding: 10px 0px;
  background: rgba(96, 92, 92, 0.312);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  display: flex;
  justify-content: space-around;
}

.dark_mode_options {
  cursor: pointer;
  width: 22%;
  border-radius: 5px;
  text-align: center;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: rgba(59, 56, 56, 0.46);
  input {
    display: none;
  }
  label {
    width: 100%;
    height: 100%;
    padding: 5px 0px;
    display: inherit;
    align-items: inherit;
    justify-content: inherit;
    font-weight: bold;
    cursor: pointer;
  }
}

#darkMode,
#appBackground {
  margin-bottom: 10px;
}

.background_items {
  width: 100%;
  padding: 10px;
  background: rgba(96, 92, 92, 0.312);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.background-selection {
  width: 100%;
  height: 35px;
  font-weight: bold;
  background: rgba(71, 68, 68, 0.456);
  border-radius: 5px;
  padding: 8px 5px;
}

.coloursCategory {
  width: 50px;
  height: 50px;
  border-radius: 5px;
  margin: 10px;
}

.category {
  margin-top: 10px;
  ul {
    padding: 0px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 0px 5px;
    .coloursFrame {
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      flex-direction: column;
      min-width: 50px;
      min-height: 50px;
      border-radius: 10px;
      transition: 0.2s linear;
      position: relative;
      cursor: pointer;
      label {
        position: absolute;
        height: 100%;
        width: 100%;
        cursor: pointer;
      }
      input {
        box-shadow: 0px 0px 1px 1px rgba(246, 246, 246, 0.523);
        width: 40px;
        height: 40px;
        border: none;
        outline: 0px;
        z-index: 1;
      }

      img {
        width: 50px;
        height: 50px;
        pointer-events: none;
      }
    }

    .coloursFrame input[type="color"] {
      position: absolute;

      visibility: hidden;
    }

    .imageFrame {
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      flex-direction: column;
      width: 140px;
      height: 140px;
      border-radius: 10px;
      transition: 0.3s ease;
      text-align: center;
      position: relative;
      label {
        position: absolute;
        height: 100%;
        width: 100%;
        cursor: pointer;
      }
    }

    .imageFrame input[type="image"],
    img {
      width: 90%;
      height: 90%;
      border-radius: inherit;
      object-fit: cover;
      z-index: 1;
    }

    img {
      pointer-events: none;
    }
  }
}

#customBackground {
  display: none;
}

/////////// security css

#show_password {
  //   border: 2px solid green;
  width: 100%;
  padding: 4px 0px;
  #show_pwd_btn {
    color: inherit;
    padding: 5px 5px;
    width: inherit;
    outline: none;
    border: none;
    background: rgba(98, 90, 90, 0.355);
    cursor: pointer;
    border-radius: 5px;
    span {
      color: inherit;
      font-family: monospace, system-ui, -apple-system;
    }
  }
}

.change_my_password,
.update_security {
  width: 100%;
  outline: none;
  border: none;
  color: inherit;
  border-radius: 5px;
  margin: 5px 0px;
  padding: 5px 10px;
  background: #888d9753;
  text-align: left;
  min-height: 80px;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: space-between;
  span {
    font-size: large;
    margin: 5px 0px;
    pointer-events: none;
  }
  i {
    font-size: larger;
  }
}

.settings_options {
  width: 100%;
  color: inherit;
  margin-bottom: 10px;
  ul {
    color: inherit;
    padding: 0px !important;
    width: 100%;
    li {
      color: inherit;
      border-radius: 5px;
      margin: 10px 0px;
      button {
        padding: 0px 10px;
      }
      #logout {
        display: none;
      }
    }
  }
}

.wide-list-btn {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;

  label {
    display: block;
    width: 100%;
    padding: 8px;
    font-weight: 600;
    color: inherit;
    span {
      font-size: 0.8em;
      font-weight: 500;
    }
  }
  input {
    position: absolute;
    right: 1%;
  }
}

.IOS-notice {
  padding: 10px;
  background: #888d9753;
  p {
    margin: 10px 0px;
  }
}

.feature_available {
  background: #888d9753;
  cursor: pointer;
  color: inherit;
}

#feature_note {
  word-wrap: break-word;
  display: inline-block;
  width: 84%;
}

.feature_available:hover {
  background: rgba(134, 134, 139, 0.247);
}

.feature_not_available {
  background: rgba(40, 40, 42, 0.206);
  cursor: not-allowed;
}

.security_form {
  height: 100%;
  color: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  input[type="submit"] {
    margin: 10px;
  }
}

.security_input_div {
  width: 100%;
  height: 100%;
}

.edit_inputs {
  color: inherit;
  // border: 0.5px solid rgb(193, 28, 28);
  width: 100%;
  margin: 2px 0px;
  padding: 0px 5px;
  label {
    font-family: monospace;
    margin-bottom: -8px;
    color: inherit;
  }
  input,
  textarea {
    background: inherit;
    color: inherit;
    padding: 5px;
    width: 100%;
    margin: 5px 0px;
    border-radius: 5px;
    border: 0.5px solid rgb(93, 90, 90);
    resize: none;
  }

  input:focus,
  textarea:focus {
    box-shadow: 0px 0px 3px 3px rgba(255, 255, 255, 0.716);
  }
}

.mobile_view {
  .hide_layout {
    display: none;
  }

  .show_layout {
    display: block;
    width: 100%;
  }
}

.selected_options {
  background: rgba(10, 219, 21, 0.443);
}

.selected_category {
  border: 10px solid rgba(10, 219, 21, 0.443);
}
#blocked_contacts {
  height: 90%;
}

#no-blocked-contacts {
  word-wrap: break-word;
  background: rgba(96, 93, 93, 0.176);
  border-radius: 10px;
  padding: 10px;
}

// help component css

#help_form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80%;

  textarea {
    border-radius: 10px;
    background: inherit;
    color: inherit;
    outline: none;
    padding: 10px;
    min-height: 50%;
    border: 2px solid rgba(118, 113, 113, 0.644);
  }

  textarea:focus {
    border: 2px solid rgb(0, 145, 255);
  }
  input {
    width: fit-content;
    margin: 10px 0px 10px auto;
  }
}

#app_About {
  width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  #app_version {
    position: absolute;
    top: 25px;
    display: inherit;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    min-height: 50px;

    img {
      width: 55px;
      height: 55px;
      border-radius: 50%;
    }
    p {
      font-size: 0.8rem;
    }
    span {
      font-size: 0.8rem;
      font-family: monospace;
    }
  }
  #Prince_info {
    position: absolute;
    bottom: 25px;
    min-height: 50px;
    text-align: center;
    p{
      margin: 0px;
      font-size: 0.9rem;
    }
  }
}

@media (max-width: 950px) {
  .settings {
    padding: 0px;
  }

  .setting_options,
  .selected_settings {
    border-radius: 0px;
  }

  .setting_options {
    background: none !important;
    border-top-right-radius: 0px;
  }

  .settings_window {
    margin-top: 0px;
    border-radius: 0px;
  }

  .selected_settings {
    width: 72%;
    margin: 0px;
    height: 100%;
    position: relative;
  }

  .category {
    ul {
      .coloursFrame {
        min-width: 70px;
        min-height: 70px;
        cursor: pointer;
        input {
          width: 35px;
          height: 35px;
        }
        label {
          font-size: 0.8em;
        }
      }

      .imageFrame {
        width: 100px;
        height: 100px;
        span {
          font-size: 0.8em;
        }
      }
    }
  }
}

@media (max-width: 520px) {
  #opened_setting {
    min-height: 90%;
  }

  .small_header {
    font-size: 1em;
  }

  .btn_inside_collapseible {
    label {
      font-size: 0.6rem;
    }
  }

  .btn_inside_collapseible {
    label {
      font-size: 0.7em !important;
    }
  }

  .change_my_password,
  .update_security {
    min-height: 50px;
    span {
      font-size: 1rem;
      margin: 5px 0px;
      pointer-events: none;
    }
    i {
      font-size: larger;
      float: right;
      margin-top: -10px;
    }
  }

  .category {
    ul {
      .coloursFrame {
        min-width: 35px;
        min-height: 35px;
        cursor: pointer;
        input {
          width: 30px;
          height: 30px;
        }
        label {
          font-size: 0.6em;
        }
        img {
          width: 30px;
          height: 30px;
        }
      }

      .imageFrame {
        width: 80px;
        height: 80px;
      }
    }
  }
}
