.manage_profile_container { 
  height: 100vh;
  width: 100%;
  position: relative;
}

#manager-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding: 0px 5px;

  #backBtn_and_heading {
    display: flex;
    align-items: center;

    h4{
       margin:0px 5px !important;
    }
    button,
    h4 {
      font-weight: 800;
      display: inline-block;
      margin:0px 0px;
      padding: 0px;
    }
  }

  ul {
    margin: 0px;
    li {
      display: inline-block;
      margin: 0px 10px;
      list-style: none;
      button {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background: none;
        outline: none;
        border: none;
        width: 32px;
        height: 32px;
        padding: 0px;
        i {
          font-size: 1.2rem;
        }
      }

      .sec0 {
        background: rgba(118, 231, 11, 0.748);
      }

      .sec1 {
        background: rgba(196, 28, 28, 0.623);
      }
    }
  }
}

#manage_profile_body {
  position: relative;
  height: calc(100% - 50px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.editable_info {
  position: relative;
  width: 50%;
  height: 95%;
  border-radius: 10px;
}

.cells_header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  pointer-events: none;
  height: 30px;
  i {
    margin: 0px;
    font-size: 1.4em;
    padding: 2px;
  }
  h3 {
    padding: 0px;
    margin: 0px;
    font-weight: 600;
  }
}

.user_info {
  width: 100%;
  height: 100%;
  padding: 10px;
  background: #4743432c;
  border-radius: 12px;
  transition: 0.3s ease;
}

.personal_info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info-div {
  height: 100%;
  width: 100%;
  padding: 5px;
  color: inherit;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  input {
    border: 1px solid rgb(86, 83, 83);
  }
}

.about {
  width: 100%;
  padding: 0px 4px;
  label {
    font-weight: bold;
    margin-left: 5px;
    font-size: 0.8rem;
  }
  textarea {
    padding: 5px;
    border: 1px solid rgb(86, 91, 90);
    outline: none;
    width: 100%;
    border-radius: 5px;
    margin-top: 0px;
    font-weight: bold;
  }
}

.avatar_div {
  position: relative;
  width: 300px;
  height: 300px;
  display: flex;
  justify-content: center;
}

#Avatar {
  overflow: hidden;
  position: relative;
  height: 80%;
  width: 80%;
}

.avatar_img {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  transition: 0.2s ease;
  pointer-events: none;
  margin: 0px !important;
  padding: 0px !important;
}

#avatar_loading {
  position: absolute;
  top: 0px;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.avatar_btn_div {
  width: 70%;
  height: 15%;
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  position: absolute;
  border-radius: 20px;
  bottom: 5px;
  left: 15%;
  background: #a2a2a8c5;
}

.avatar_btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: none;
  border: none;
  outline: none;
  width: 35px;
  height: 35px;
  text-decoration: none;
  cursor: pointer;
  padding: 0px;
  i {
    color: inherit;
  }
}

.avatar_btn:nth-child(1) {
  background: rgba(23, 105, 28, 0.64);
}

.avatar_btn:nth-child(2) {
  background: rgba(138, 26, 26, 0.623);
}

.avatar_btn:nth-child(3) {
  background: rgba(16, 41, 81, 0.748);
}

#update_avatar {
  display: none;
  background: rgb(12, 94, 175);
}

.profile_save_button {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  p {
    margin: 5px;
  }
  input {
    height: 40px;
    width: 100%;
  }
}

.selected {
  background: #7d828953 !important;
}

/////////////////////////// email , user name and delete ac scssss

#access-div {
  margin-top: 100px;
}

#delete_action {
  height: auto;
  margin: 10px 0px;
}

#delete_btn {
  cursor: pointer;
  background: rgb(152, 42, 42);
}

@media (max-width: 1250px) {
  .editable_info {
    width: 80%;
  }
}

@media (max-width: 950px) {
  .editable_info {
    width: 100%;
    height: 100%;
    display: inline-block;
  }

  .cells_header {
    img,
    h5 {
      margin: 0px;
    }
  }

  .user_info {
    width: 100%;
    background: none;
  }

  .avatar_div {
    height: 300px;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }

  #Avatar {
    width: 80%;
    max-height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .avatar_img {
    border-radius: 50%;
  }

  .avatar_btn_div {
    height: auto;
    width: 80%;
    position: relative;
    bottom: 0px;
    left: 0px;
  }

  .info-div {
    width: 100%;
    padding: 0px;
  }

  .profile_save_button {
    margin: 0px;
  }

  /// account & delete  css

  .inputs-div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 30%;
  }
}

@media screen and (max-width: 520px) {
  .avatar_div {
    height: 180px;
    width: 180px;
  }

  #Avatar {
    width: 70%;
    max-height: 70%;
  }

  .avatar_btn {
    width: 30px;
    height: 30px;
  }

  #manager-header {
    ul {
      li {
        margin: 0px 5px;
        button {
          width: 30px;
          height: 30px;
          i {
            font-size: 1.2rem;
          }
        }
      }
    }
  }
}
