@import "/src/App.scss";

.chats_container {
  position: relative;
  width: inherit;
  height: calc(100vh - 50px);
  padding: 0px;
  border-radius: 12px;  
}

.contacts-header {
  background: inherit;
  height: 25px;
  background: #888d9738;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding-left: 5px;

  h5 {
    display: inline-block;
    padding: 0px;
    margin: 0px;
  }
}

.chats {
  position: relative;
  height: calc(100% - 25px);
}

.searchLayout {
  padding: 5px;
  position: relative;

  .search-result {
    margin-bottom: 5px;
    h2 {
      font-weight: 100;
      padding: 0px;
      margin: 5px;
    }
    hr {
      margin: 0px;
      opacity: 0.2;
    }
  }
}
