@import "/src/App.scss";

.chat-container {
  height: 100vh;
  transition: 0.3s ease;
  overflow: hidden !important;
  position: relative;
  padding: 5px;
}

.hide_chat {
  display: none;
}

.wider {
  width: calc(100% - 45px);
}

.shrink {
  width: 80%;
}

.chat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px;
  height: 45px;
}

.chat-header-Left {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.checkProfile {
  overflow: hidden;
  display: flex;
  align-items: center;
  border-radius: 10px;
  padding: 2px;
  cursor: pointer;
  #currentChatDp-icon {
    margin-right: 5px;
    height: 35px;
    width: 35px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .chatWith {
    overflow: hidden;
    width: fit-content;
    max-width: 175px;
    text-align: start;
    line-height: 14px;
    p {
      margin: 0px;
      padding: 0px;
      font-size: 0.8rem;
      font-weight: bold;
      overflow: inherit;
    }

    .online_status {
      font-size: 0.6rem;
      font-weight: bold;
    }
  }
}

.chat_actions {
  display: flex;
  align-items: center;
  margin-right: 5px;

  .dropdown_div {
    .dropdown_button {
      padding: 5px 15px !important;
    }

    .dropdown_menu {
      max-width: 200px;
      backdrop-filter: blur(19px) saturate(180%);
      -webkit-backdrop-filter: blur(19px) saturate(180%);
      background-color: rgba(255, 255, 255, 0.987);
      border: 1px solid rgba(255, 255, 255, 0.125); 
      border-radius: 12px;
      list-style-type: none;
      padding: 0;
      margin: 0;
      position: absolute;
      right: 10px;
      color: inherit;
      overflow: hidden;
      background: inherit;
      z-index: 1;
    }

    .dropdown-items {
      padding: 12px;
      cursor: pointer;
      font-size: 0.8em;
      font-weight: bold;
      i {
        vertical-align: middle;
        margin-right: 3px; 
        padding: 1px;
      }
    }

    .dropdown_active {
      background: rgba(106, 99, 99, 0.621);
    }
  }
}

.messages-container {
  width: 100%;
  height: calc(100% - 95px);
  border-radius: 10px;
  user-select: none;
  margin: 2px 0px;
  scroll-behavior: smooth;

  .message-date {
    background: #63646612;
    border-radius: 8px;
    text-align: center;
    padding: 5px;
    position: sticky;
    top: 0px;
    color: inherit;
    span {
      background: rgb(112, 111, 111);
      padding: inherit;
      border-radius: 8px;
      font-size: 0.8rem;
      font-weight: bold;
    }
  }

  .left {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 2px;
    .message {
      background: #c604d0;
    }
  }
  .right {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 2px;
    .message {
      background: #0261c7;
    }
  }

  .left,
  .right {
    border-radius: 10px;
  }

  .selected {
    background: #5d15ed7b !important;
    border-top: 1px solid #7975707c !important;
    border-bottom: 1px solid #7975707c !important;
    border-radius: 10px;
  }

  .unselected {
    background: #2c50ab !important;
  }

  .message {
    width: max-content;
    max-width: 70%;
    height: auto;
    padding: 5px;
    border-radius: 8px;
    word-wrap: break-word;
    margin: 10px 0px 10px 0px;
    animation: message 0.2s ease-in-out;
    p {
      font-size: 0.8rem;
      display: inline;
      font-weight: bold;
    }
    .msg_info {
      border-radius: $default_radius;
      background: rgba(154, 130, 130, 0.102);
      margin-left: 5px;
      padding: 0px 2px 0px 2px;
      float: right;

      .msg_time {
        font-size: 0.5rem;
        vertical-align: middle;
      }
    }
    .msg_option {
      display: inline-block;
      border: 0.2px solid rgb(38, 255, 0);
      width: 100%;
      i {
        margin: 0px 5px;
      }
    }
  }

  @keyframes message {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  .unknown-contact {
    background: rgba(154, 130, 130, 0.225);
    border-radius: 12px;
    text-align: center;
    display: flex;
    flex-direction: column;
    padding: 2px;
    p {
      margin: 0px;
    }
    .unknown-contact-action {
      padding: inherit;
      button {
        border-radius: 12px;
        margin: 0px 10px;
        background: initial;
        font-weight: bolder;
      }
      #blockNow {
        border: 2px solid red;
        color: rgb(248, 2, 2);
      }
      #addNow {
        border: 2px solid green;
        color: rgb(0, 190, 0);
      }

      #blockNow:hover {
        background: red;
        color: rgb(255, 255, 255);
      }
      #addNow:hover {
        background: green;
        color: rgb(255, 255, 255);
      }
    }
  }
}

#message-inputs {
  width: 100%;
  height: 45px;
  display: flex;
  align-items: center;
  padding: 2px;
  border-radius: 12px;

  transition: 0.3s ease;

  #type_message {
    border: 0.2px solid rgba(92, 85, 85, 0.735);
    border-radius: 12px;
    width: 100%;
    height: 100%;
    resize: none;
    outline: none;
    line-height: normal;
    font-size: 1rem;
    padding: 10px;
    font-family: monospace;
    background: inherit;
    color: inherit;
    transition: 0.3s ease;
    scroll-behavior: smooth;
    margin-right: 5px;
  }

  #type_message:hover {
    background: #686a6f53;
  }
  #type_message:focus {
    background: #686a6f53;
  }

  .send {
    width: 45px;
    height: 100%;
    border-radius: 12px;
    border: none;
    background: #888d9741;
    padding: 5px;
  }
}

#its_blocked {
  background: #888d9753;
  border-radius: 10px;
  text-align: center;
  padding: 5px 0px;

  p {
    margin: 0px;
  }
}

@media (max-width: 950px) {
  .wider {
    width: 100%;
  }
}
