@import "/src/App.scss";

.sideChats {
  height: 100vh;
  padding: 5px;
  transition: 0.3s ease;
  background: rgba(187, 188, 190, 0.221) !important;
  overflow: hidden;
}

.collapsed {
  padding: 2px;
  width: 45px;
  .no-side-contacts {
    display: none !important;
  }
  span {
    display: none;
  }

  #collapse_button {
    width: 100%;
  }

  #sideChats-header {
    padding: 0px;
    background: none;
  }

  #collapse_button {
    i {
      vertical-align: middle;
    }
  }

  .side-chat-info {
    display: none;
  }
  .side-chat-container {
    .side-chat-contact {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 5px 0px;

      .onlineCircle {
        border: 3px solid rgb(0, 184, 0);
      }
      .side-chat-avatar {
        width: 25px;
        height: 25px;
        margin: 0px;
      }
    }
  }
}

.expend {
  width: 20%;
}

#sideChats-header {
  width: 100%;
  height: 30px;
  background: #888d9753;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
  span {
    margin: 2px 0px 0px 5px;
    overflow: hidden;
  }
}
.small_contact_name {
  font-size: 0.8em !important;
  display: flex;
}

.userTyping {
  color: rgb(0, 125, 220);
}

.userOnline {
  color: rgb(2, 165, 16);
}

#collapse_button {
  background: none !important;
  border: none;
  outline: none;
  padding: 0px;
  color: inherit;
  width: 25px;
  height: 25px;
  i {
    vertical-align: sub;
    font-size: 1.2rem;
  }
}

.side-chat-container {
  transition: 0.3s ease;
  margin-top: 5px;
  height: calc(100% - 35px);
}

.side-chat-list {
  margin: 0px;
  padding: 0px;
}

.side-chat-contact {
  cursor: pointer;
  list-style: none;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 10px;
  margin-bottom: 5px;
}

.side-chat-avatar {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin-right: 5px;
}

.side-chat-info {
  width: 75%;
  .side-contact-name-count {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
      font-size: 0.8rem;
      font-weight: bold;
      margin: 0px;
      display: inline-block;
    }
    .side-unread_msg_count {
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgb(46, 144, 230);
      min-width: 15px;
      height: 15px;
      border-radius: 5px;
      font-size: 0.6em;
    }
    .no-unread-msg {
      visibility: hidden !important;
    }
  }
  .side-contact-last-info {
    span {
      font-size: 0.6rem;
      font-weight: bold;
    }
  }
}

@media (max-width: 950px) {
  .sideChats {
    display: none;
  }
}
