#verification-Box {
  min-height: 50%;
  max-width: 95%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  transition: 0.3s linear;
  padding: 10px;
}

.verify_email {
  text-align: center;
  font-weight: bold;
  color: rgb(66, 59, 59);
  height: 10%;
}

#verify-illustration {
  height: 25%;
  i{
    font-size: 3rem;
    color: #1f98f5;
  }
}

#hint {
  border: 1px solid rgb(20, 156, 20);
  word-wrap: break-word;
  min-height: 30px;
  padding: 5px;
  font-weight: 600;
  background: #5aab6d82;
  border-radius: 10px;
  color: rgb(12, 53, 12);
  text-align: center;
  p {
    margin: 0px;
  }
}

.verificaion-input-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
  height: 60%;
  transition: 4s linear;
}

.form_btns {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input-buttons {
  height: 45px;
  width: 200px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border-radius: 5px;
  font-weight: 500;
  margin: 5px;
  border: none;
  transition: 0.3s ease;
  font-size: 1rem;
  cursor: pointer;
}

#submit {
  background: rgb(10, 131, 224);
}

#submit:hover {
  background: rgb(6, 140, 243);
}

#resend {
  background: rgb(2, 156, 2);
}

#resend:hover {
  background: rgb(3, 197, 3);
}

#cancel {
  border: none;
  background: transparent;
  color: #0e93f8;
  font-size: 0.9em;
  text-decoration: dashed;
  margin: 0px;
  cursor: pointer;
}

#cancel:hover {
  text-decoration: underline;
}
