.search-box {
  width: 100%;
  height: 35px;
  margin: 0px;
  position: relative;
  display: flex;
  border-radius: 10px;
  background: rgba(112, 110, 110, 0.13);
}

.input-search {
  background: inherit;
  color: inherit;
  height: 35px;
  width: 100%;
  border-style: none;
  font-size: 1.2rem;
  outline: none;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
  padding: 0px 10px;
}

.disable-search {
  width: 35px;
  background: transparent;
  border: none;
  outline: none;
  color: inherit;
  i {
    vertical-align: middle;
  }
}

/* For WebKit-based browsers (Chrome, Safari) */
input[type="search"]::-webkit-search-cancel-button {
  cursor: pointer;
  margin-left: 5px;
}

/* For Firefox */
input[type="search"]::-moz-search-clear-button {
  cursor: pointer;
  margin-left: 5px;
}
