#userProfileLayout {
  z-index: 1;
  border-radius: 10px;
  position: absolute;
  top: 0px;
  left: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background: transparent !important;
}

.hideProfileBtn {
  position: absolute;
  top: 10px;
  right: 10px;
}

#forBlurBackground {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  position: absolute;
  border-radius: 12px;
  max-height: 90%;
  height: auto;
  width: 40%;
}

#forBlurBackground {
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
}
#forBlurBackground::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}

.contactInfo {
  z-index: 1;
  position: relative;
  top: 0px;
  bottom: 0px;
  backdrop-filter: blur(50px) saturate(200%);
  -webkit-backdrop-filter: blur(10px) saturate(50%);
  background-color: rgba(255, 255, 255, 0.349);
  box-shadow: rgba(252, 2, 2, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 10px 5px;
  border-radius: inherit;
  word-wrap: break-word;
}

.userAvatar { 
 width: 200px;
  height: 200px;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}

.contactUsername {
  width: 100%;
  height: auto;
  padding: 10px;
  text-align: center;
  h1 {
    font-size: 2em;
    word-wrap: break-word;
    margin: 0px;
  }
}

#allInfo {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.small-info-container {
  width: fit-content;
  max-width: 100%;
  border-radius: 5px;
  margin: 10px 20px;
  .small-info-header {
    height: 25px;
    background: rgba(105, 92, 92, 0.144);
    border-radius: 5px;
    padding: 0px 15px 0px 5px;
    span {
      font-size: 1em;
      font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
        sans-serif;
      color: inherit;
    }
  }
  .small-info {
    p {
      margin: 3px;
      word-wrap: break-word;
      font-weight: bold;
    }
  }
}

@media (max-width: 950px) {
  #forBlurBackground {
    width: 60%;
  }

  .userAvatar {
    width: 150px;
    height: 150px;
  }
}

@media (max-width: 550px) {
  #forBlurBackground {
    width: 80%;
  }

  .userAvatar {
    width: 100px;
    height: 100px;
  }
}
